import './App.css';
import React, { Component } from 'react'
import Movierow from './moviesrow.js'
import $, { event } from 'jquery'
import Footer from './footer'
import Oauth from './oauth'

export class App extends Component {

  constructor(props) {
    super(props)
    this.state = {}

    this.search('a')
  }

  search(input) {
    const api = 'https://api.themoviedb.org/3/search/movie?api_key=161a45e74e056bb50759cf60f15ae920&language=en-US&query=' + (input) + '&page=1&include_adult=true';
    $.ajax({
      url: api,

      success: (result) => {
        console.log('sucess')

        const resl = result.results
        var moviesrow = [];

        resl.forEach(
          (movie) => {
            // console.log(movie);
            movie.img = 'https://images.tmdb.org/t/p/w185' + movie.poster_path
            const mov = <Movierow key={movie.id} movie={movie} />

            moviesrow.push(mov)
          }
        )
        this.setState({ row: moviesrow })

      },
      error: (xhr, status, err) => {
        console.error("failed")
      }
    }
    )
  }

  input(event) {
    // console.log(event.target.value);
    const sr = event.target.value
    //bound down at input binds the input to this to
    const bound = this
    bound.search(sr)

  }

  render() {
    return (
      <>

        <table className='header'>
          <tbody>
            <tr>
              <td className='header_img'><img width='40' src="data:image/png;base64," /></td>
              <td className='header_img'>Movies Data Base</td>
              {/* <img src="../public.favicon.ico" alt="" srcset=""/> */}
              <td className='header_img'><Oauth /></td>
            </tr>
          </tbody>
        </table>

        <input placeholder='Enter movie name' onChange={this.input.bind(this)} />

        {this.state.row}
        <Footer />

      </>
    );
  }
}

export default App;
